.player {
  position: absolute;
  width: 35px;
  top: 15px;
  left: 15px;
  cursor: pointer;
  z-index: 1;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.modal {
  position: relative;
  width: 70%;
  /*min-height: 200px;*/
  max-height: 70vh;
  color: white;
}

.modal-content {
  overflow-y: auto;
  overflow-x: hidden;
  padding: 25px;
  background-color: rgba(27, 11, 36, 0.59);
  max-height: 70vh;
}

.close-container {
  position: absolute;
  top: -50px;
  right: -40px;
  width: 50px;
  height: 50px;
}

.close {
  font-size: 45px;
  font-weight: 600;
  display: inline-block;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  cursor: pointer;
}

a {
  text-decoration: none;
}

.logo-container {
  background: #eebb5c;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
}

ul {
  padding-left: 0;
  list-style: none;
}

ul li {
  height: 50px;
  position: relative;
  margin: 10px 0;
  cursor: pointer;
}

ul li .fa {
  font-size: 20px;
  color: #fff;
}

.instagram div.logo-container {
  background: #bc2a8d;
}

.instagram div.slider {
  background: #dd94c6;
}

.telegram div.logo-container {
  background: #229ED9;
}

.telegram div.slider {
  background: #90ceec;
}

.soundcloud div.logo-container {
  background: #ff7700;
}

.soundcloud div.slider {
  background: #ffc490;
}

.bandcamp div.logo-container {
  background: #629aa9;
}

.bandcamp div.slider {
  background: #91B8C2;
}

.email div.logo-container {
  background: #629aa9;
}

.email div.slider {
  background: #91B8C2;
}

.zaycev div.logo-container {
  background: #2f4f59;
}

.zaycev div.slider {
  background: #91B8C2;
}

.youtube div.logo-container {
  background: #bb0000;
}

.youtube div.slider {
  background: #282828;
}

.slider {
  content: "";
  max-width: inherit;
  position: absolute;
  top: 0;
  left: 51px;
  width: 0;
  height: 50px;
  background: #eebb5c;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.slider p {
  font-family: arial, serif;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
  color: #fff;
  text-align: center;
  margin-top: 0.4rem;

  opacity: 0;
}

ul li:hover .slider {
  width: 94%;
}

ul li:hover .slider p {
  opacity: 1;
}

p {
    text-align: justify-all;
}

h3 {
    text-align: center;
}

.about-us {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1;

    background-color: #EA4C89;
    border-radius: 8px;
    border-style: none;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
    font-weight: 500;
    height: 40px;
    line-height: 20px;
    list-style: none;
    margin: 0;
    outline: none;
    padding: 10px 16px;
    text-align: center;
    text-decoration: none;
    transition: color 100ms;
    vertical-align: baseline;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.about-us:hover,
.about-us:focus {
    cursor: pointer;
    background-color: #F082AC;
}

@media (max-width: 480px) {
    .about-us {
        top: 12%;
        right: 40%;
    }
}

